import React, { useState } from "react";

import "./PortfolioWork.css";

import PortfolioCardsRow from "../portfolio-cards-row/PortfolioCardsRow";

import pic1 from "./pic1.jpg";
import pic2 from "./pic2.jpg";
import pic3 from "./pic3.jpg";

import webPic1 from "./webPicOne.png";
import webPic2 from "./webPicTwo.png";
import webPic3 from "./webPicThree.png";
import webPic4 from "./webPicFour.png";
import webPic5 from "./webPicFive.png";
import webPic6 from "./webPicSix.jpg";

import socialPic1 from "./socialOne.jpg";
import socialPic2 from "./socialTwo.jpg";
import socialPic3 from "./socialThree.jpg";
import socialPic4 from "./socialFour.jpg";
import socialPic5 from "./socialFive.jpg";
import socialPic6 from "./socialSix.jpg";
import socialPic7 from "./socialSeven.jpg";

import graphicOne from "./graphicOne.webp"
import graphicTwo from "./graphicTwo.webp"
import graphicThree from "./graphicThree.webp"
import graphicFour from "./graphicFour.webp"
import graphicFive from "./graphicFive.png"
import graphicSix from "./graphicSix.png"
import graphicSeven from "./graphicSeven.png"
import graphicEight from "./graphicEight.png"
import graphicNine from "./graphicNine.png"
import graphicTen from "./graphicTen.png"

export default function PortfolioWorks() {
  const imagesForAll = [pic1, pic2, pic3, pic3];

  const imagesForWebDesign = [webPic1, webPic2, webPic3, webPic4, webPic5, webPic6];

  const imagesForSocial = [socialPic1, socialPic2, socialPic3, socialPic4, socialPic5, socialPic6, socialPic7];

  const imagesForGraphic = [graphicOne, graphicTwo, graphicThree, graphicFour, graphicFive, graphicSix, graphicSeven, graphicEight, graphicNine, graphicTen];

  const [buttonPressed, setButtonPressed] = useState(1);

  return (
    <div className="portfolio-work-main-container">
      <div className="portfolio-work-main-container-button">
        <button
          style={{
            backgroundColor: `${buttonPressed === 1 ? "#612d91" : "#fff"}`,
            color: `${buttonPressed === 1 ? "#fff" : "#612d91"}`,
          }}
          onClick={() => {
            setButtonPressed(1);
          }}
        >
          All
        </button>

        <button
          style={{
            backgroundColor: `${buttonPressed === 2 ? "#612d91" : "#fff"}`,
            color: `${buttonPressed === 2 ? "#fff" : "#612d91"}`,
          }}
          onClick={() => {
            setButtonPressed(2);
          }}
        >
          Web Design
        </button>

        <button
          style={{
            backgroundColor: `${buttonPressed === 3 ? "#612d91" : "#fff"}`,
            color: `${buttonPressed === 3 ? "#fff" : "#612d91"}`,
          }}
          onClick={() => {
            setButtonPressed(3);
          }}
        >
          Social Media Marketing
        </button>

        <button
          style={{
            backgroundColor: `${buttonPressed === 4 ? "#612d91" : "#fff"}`,
            color: `${buttonPressed === 4 ? "#fff" : "#612d91"}`,
          }}
          onClick={() => {
            setButtonPressed(4);
          }}
        >
          Graphic Design
        </button>

        <button
          style={{
            backgroundColor: `${buttonPressed === 5 ? "#612d91" : "#fff"}`,
            color: `${buttonPressed === 5 ? "#fff" : "#612d91"}`,
          }}
          onClick={() => {
            setButtonPressed(5);
          }}
        >
          Video Animation
        </button>
      </div>

      {buttonPressed === 1 && (
        <PortfolioCardsRow numberOfCards={12} pictures={imagesForAll} initialImagesToShow={3} />
      )}

      {buttonPressed === 2 && (
        <PortfolioCardsRow numberOfCards={6} pictures={imagesForWebDesign} initialImagesToShow={3}/>
      )}

      {buttonPressed === 3 && (
        <PortfolioCardsRow numberOfCards={7} pictures={imagesForSocial} initialImagesToShow={3}/>
      )}

      {buttonPressed === 4 && (
        <PortfolioCardsRow numberOfCards={10} pictures={imagesForGraphic} initialImagesToShow={3}/>
      )}

      {buttonPressed === 5 && (
        <PortfolioCardsRow numberOfCards={8} pictures={imagesForAll} initialImagesToShow={3}/>
      )}
    </div>
  );
}
