import React from "react";

import "./cardTwo.css";

import pic1 from "./pic1.jpg";

export default function CardTwo(prop) {
  return (
    <div className="card-two-main">
      <div className="bottom-div"></div>

      <div
        className="top-div"
        style={{ backgroundImage: `url(${prop.backgroundImage})` }}
      >
        <div className="top-div-partition-one">
          <h1>{prop.heading}</h1>

          <h2>{prop.paragraph}</h2>
        </div>

        {/* <div className="top-div-partition-two"></div> */}
      </div>
    </div>
  );
}

CardTwo.defaultProps = {
  backgroundImage : pic1,
};
